.page-view-control {
    background:#9B7D0F;
    padding-left:16px;
    padding-right:16px;
    padding-top:5px;
    position:fixed;
    display:flex;
    bottom:0;
    left:0;
    width:100%;
    z-index:100;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 500;
    line-height: 1.6;
    justify-content: space-between;
}
